<template>
	<section>
      <span class="title">{{$t('question.question')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm">
        	<el-form-item :label="$t('problemClass')">
			      <!-- <el-input v-model="userForm.number"></el-input> -->
             <el-select v-model="select"  :placeholder="$t('Pchoose')">
                     <el-option   v-for="(item,index) in problem" :label="item" :value="index+1" :key="index"></el-option>
              </el-select>
        	</el-form-item>
    	    <el-form-item :label="$t('CTFT')">
            <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.startTime"></el-date-picker>              
          </el-form-item>
          <el-form-item :label="$t('go')">
            <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.endTime"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button class="warning" v-on:click="getUsers">{{$t('search')}}</el-button>
          </el-form-item>
          <el-form-item>
                 <el-button class="info"  @click="handleReset">{{$t('userList.register[6]')}}</el-button>                 
          </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="id"  :label="$t('question.thead[0]')">
            </el-table-column>
            <el-table-column prop="userName" :label="$t('question.thead[1]')">
            </el-table-column>
            <el-table-column prop="email"  :label="$t('question.thead[2]')">
            </el-table-column>
            <el-table-column prop="problemType"  :label="$t('question.thead[3]')">
            </el-table-column>
            <el-table-column prop="description" class="description" :label="$t('question.thead[4]')" width="250px">
            </el-table-column>
            <el-table-column prop="utime"  :label="$t('question.thead[5]')">
            </el-table-column>
            <el-table-column prop="enclosure"  :label="$t('question.thead[7]')" >
              <template slot-scope="scope">
                <i class="iconfont icon-fujian"  @click="checkAccessory(scope.$index,scope.row)" ></i>
              </template>
            </el-table-column>
      </el-table>
      <!-- 查看附件弹窗 -->
      <el-dialog :title="$t('checkTheAttachment')" :visible.sync="accessoryShow"  width="30%" >
        <span v-if="enclosure =='没有附件'">{{enclosure}}</span>
        <div v-else class="enclosure">
          <template v-for="(item,index) in enclosure">
            <p>{{$t('filename')}}:<a :href="enclosure[index].url "> {{enclosure[index].name}} </a></p>
            <p>{{$t('size')}}:{{enclosure[index].size}}</p>
          </template> 
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button   type="primary" @click="accessoryShow = false">{{$t('close')}}</el-button>
        </span>
      </el-dialog>
      <!-- <div class="page" v-show="userList.length!=0"> -->
      <div class="page" v-show="total >10">
        <el-col :span="24" class="toolbar  ">
          <el-pagination background  layout="prev, pager, next"  :total="total" @current-change="handleCurrentChange">
          </el-pagination>
        </el-col>
      </div>
   </section>
</template>
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'
export default {
 data(){
   return{
      accessoryShow:false,
      enclosure:null,
      select:"",
      userForm:{
        number:"",
        startTime:'',
        endTime:'',
        enterpriseName:''
       },
       startT:"",
       endT:"",
      userLoading:false,
      userList:[],
      problem:["登录/注册",'交易',"提取黄金",'充币','提币','认证问题','资产','其他问题'],
      feedbacksData:{
        page:1,
        rowNum:10,
        problemType:"",
        startTime:"",
        endTime:""
       },
       total:0,
    }
  },
 methods:{
    queryFeedbacks:function(){
         if(this.userForm.number != ""){
           this.feedbacksData.problemType = this.userForm.number;
        }
        if(this.userForm.startTime != ""){
           this.feedbacksData.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
           this.startT = dateStrSecond(this.feedbacksData.startTime);
         }
         if(this.userForm.endTime != ""){
           this.feedbacksData.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
           this.endT = dateStrSecond(this.feedbacksData.endTime);
         }
         let  data = {
          page:this.feedbacksData.page,
          rowNum:this.feedbacksData.rowNum,
          problemType:this.select,
          startTime:this.startT,
          endTime:this.endT,
         }
      ajax(this,this.ApiConfig.feedback.queryFeedbacks,data, (res) =>{
            if(res.code == 200){
              console.log(res)
              this.userList = res.data.rows;
              let arr = this.userList;
              arr.forEach((item,i,arr)=>{
                 arr[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
                 arr[i].utime = getDataYear('Y-M-D h:m:s',arr[i].utime);
                if(arr[i].description.length>16){
                  arr[i].description = "描述过长请看附件" 
                } else{
                  arr[i].description =arr[i].description
                }
                switch(arr[i].problemType){
                  case 1:
                    arr[i].problemType="登录/注册";
                    break;
                  case 2:
                    arr[i].problemType="交易";
                    break;
                  case 3:
                    arr[i].problemType="提取黄金";
                    break;
                  case 4:
                    arr[i].problemType="充币";
                    break;
                  case 5:
                    arr[i].problemType="提币";
                    break;
                  case 6:
                    arr[i].problemType="认证问题";
                    break;
                  case 7:
                    arr[i].problemType="资产";
                    break;
                  case 8:
                    arr[i].problemType="其他问题";
                    break;
                }
              })
            this.total =res.data.total;
            }else{
              this.$notify({
                title:"提示",
                message:res.message,
                type:"warning"
              })
              return false;
            }
            
      },"POST")
    },

   checkAccessory:function(index,row){
      // console.log(row.enclosure)
        this.enclosure =row.enclosure;
       // this.enclosure =  this.enclosure.slice(1,-1);
       // console.log(JSON.parse(this.enclosure))
       this.enclosure =JSON.parse(this.enclosure)
       if(this.enclosure.length == "0"){
          this.enclosure ="没有附件"
       }
      this.accessoryShow=true;
    },
     getUsers:function(){
      this.queryFeedbacks();
     },
  handleReset:function(){

      this.userForm.startTime="";
      this.userForm.endTime="";
      this.userForm.page="1";
      this.userForm.rowNum=10;
      this.userForm.problemType = "";
      this.startT = "";
      this.endT = "";
      this.select="";
      this.input = "";
      this.queryFeedbacks();
     },
   tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
    handleCurrentChange:function(val){
       this.feedbacksData.page = val;
       this.queryFeedbacks();
     },

   },
   mounted:function(){
    this.queryFeedbacks();
   }
}
</script>

<style scoped lang="postcss">
.icon-fujian{
  height: 100%;
  width: 100%;
  padding:0 20px;
}
.enclosure{
 height:300px;
 overflow: auto;
}
</style>






